import React from "react";
import { AccountCircle } from "@mui/icons-material";
import "../rightSection/styles/RightSection.css";
import { Col, Row } from "react-bootstrap";

const Profile = () => {
  return (
    <>
      <div className="right-heading">
        <Row style={{ padding: "0px 8px 0px 8px" }}>
          <Col lg="2" style={{ width: "70px" }}>
            <AccountCircle className="account-circle" />
          </Col>
          <Col>
            <p className="p2">Profile</p>
          </Col>
        </Row>
      </div>
      <p className="p3">
        Fundamentos en ingeniera de sistemas, telemtica, comunicaciones,
        computacin, electrnica y el campo de las telecomunicaciones.
        Conocimientos en diseo, desarrollo e implementacin de sistemas
        informticos aplicando diversas tecnologas y utilizando herramientas de
        software y hardware para su despliegue, adems de contar con habilidades
        para disear, estructurar y gestionar bases de datos relacionales.
        Aptitudes y destrezas referentes a ciencia y al mbito tecnolgico para
        aportar con soluciones a problemas reales. Capacidades gerenciales para
        el rea de sistemas y administrar proyectos de desarrollo.
      </p>
      <div className="clearfix"></div>
      <br />
    </>
  );
};

export default Profile;
